@import url(https://fonts.googleapis.com/css2?family=Lovers+Quarrel&display=swap);
  
/* base styles */
body {
  font-family: 'Lovers Quarrel', cursive;
  margin: 0;
  font-size: 2.5em;
  text-align: center;
  background: #1b1523;
  color: #fff;
}

.card {
    position: relative;
    width: 100%; 
    padding-top: 100%; 
    overflow: hidden;    
    border: 2px solid #fff;
    border-radius: 6px;
}

.card img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
}

/* hiding and showing the picture */

.card .front{
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}

.flipped .front{
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

.card .back{
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}

.flipped .back{
    transform: rotateY(90deg);
    transition-delay:0s;
}

/* Typing Animation */
.typewriter span {
  opacity: 0;
  display: inline-block;
  animation: typing 0.2s forwards;
}

@keyframes typing {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Other styles */
.start-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 20px;
  background-color: #000;
}

.left-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 20px;
}

.dialogue-box {
  width: 600px;
  height: 150px;
  overflow: hidden;
  position: relative;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 20px;
}

.buttons-container {
  display: flex;
  width: 100%;
  justify-content: space-around;
}

button {
  padding: 10px 20px;
  margin-bottom: 10px;
  border: none;
  border-radius: 5px;
  background-color: #1b1523;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #c23866;
}

.start-screen video {
  max-height: 80vh;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7); 
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.overlay img {
  max-width: 90%;
  max-height: 90%;
  cursor: pointer;
  z-index: 1000;
}

